<template>
  <div>
    <v-ons-list>
      <v-ons-list-header>
        <b>Sales</b>
      </v-ons-list-header>
    </v-ons-list>
    <div v-if="this.portrait">
      <p>Het is week {{ thisweek }} en de totale sales bedraagt € {{ this.numberFormat(sales2019[51]) }} van target € {{ this.numberFormat(thisweek/52*400000) }} ({{ this.numberFormat(sales2019[51]/(thisweek/52*400000) * 100) }}%)</p>
      <p>
        <img src="../../static/img/icons/rotate_screen.svg" height="40" width="40">
      </p>
    </div>
    <div v-if="!this.portrait">
      <highcharts :options="optionsChart1"></highcharts>
    </div>
    <v-ons-alert-dialog
      modifier="rowfooter"
      :title="'Foutmelding'"
      :footer="{
        Ok: () => {alertDialogVisible = false; $router.push('/');}
      }"
      :visible.sync="alertDialogVisible"
    >Dit was fout</v-ons-alert-dialog>
  </div>
</template>

<script>
import axios from "axios";
import Highcharts from "highcharts"; // eslint-disable-line

import { sales2016, sales2017, sales2018 } from "./variables";

export default {
  data() {
    return {
      alertDialogVisible: false,
      sales2016,
      sales2017,
      sales2018,
      sales2019: [],
      optionsChart1: {
        chart: {
          type: "bar"
        },
        credits: {
          enabled: false
        },
        title: {
          useHTML: true,
          text: "<div uk-spinner />"
        }
      }
    };
  },
  computed: {
    thisweek: function() {
      return this.getWeekNumber(new Date());
    },
    portrait: function() {
      return this.$ons.orientation.isPortrait();
    }
  },
  created() {
    this.fetchData();
    window.addEventListener("resize", this.handleResize);
  },
  ready() {
    this.$ons.orientation.on("change", () => {
      console.log(this.$ons.orientation.isPortrait());
      this.$router.go(0);
    });
  },
  beforeDestroy() {
    this.$store.commit("setSales1", this.optionsChart1);
  },
  methods: {
    handleResize() {
      this.$router.go(0);
    },
    numberFormat(n) {
      return n.toLocaleString("nl-NL", { maximumFractionDigits: 0 });
    },
    fetchData() {
      if (this.$store.getters.salesChart1.series) {
        this.optionsChart1 = this.$store.getters.salesChart1;
        return;
      }
      let that = this;
      const PipedriveToken = sessionStorage.getItem("pipedrive-token");
      if (!PipedriveToken) {
        this.alertDialogVisible = true;
        return;
      }
      // this.sales2016.reduce(function(a,b,i) { return this.sales2016[i] = a+b; },0); //make cumulative
      const url =
        "https://api.pipedrive.com/v1/deals/timeline?start_date=2018-12-31&interval=week&amount=52&field_key=won_time&api_token=" +
        PipedriveToken;
      axios({
        url,
        // adapter: jsonp, // application, geen jsonp zetten
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(res => {
          // console.log(res);
          if (res.data.success === "false") {
            that.errorMessage = "no success in collecting data";
          } else {
            this.parseSales(res);
          }
          //   console.log(res);
        })
        .catch(error => {
          this.errorMessage = error;
          console.log(error.message);
        });
    },
    parseSales(res) {
      let sales = [];
      for (var i = 0; i < res.data.data.length; i++) {
        sales.push(res.data.data[i].totals.values.EUR);
        if (sales[i] === undefined) {
          sales[i] = 0;
        }
      }
      sales.reduce(function(a, b, i) {
        return (sales[i] = a + b);
      }, 0);
      this.sales2019 = sales;
      this.drawChart(sales);
    },
    getWeekNumber(d) {
      // Copy date so don't modify original
      d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
      // Set to nearest Thursday: current date + 4 - current day number
      // Make Sunday's day number 7
      d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay() || 7));
      // Get first day of year
      var yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
      // Calculate full weeks to nearest Thursday
      var weekNo = Math.ceil(((d - yearStart) / 86400000 + 1) / 7);
      // Return array of year and week number
      return weekNo;
    },
    drawChart(sales) {
      const currentWeek = this.getWeekNumber(new Date());
      const chartTitle = "Sales";
      const series1 = this.sales2016;
      const series2 = this.sales2017;
      const series3 = this.sales2018;
      const series4 = sales;
      // container1
      this.optionsChart1 = {
        chart: {
          type: "line"
        },
        credits: {
          enabled: false
        },
        title: {
          text: chartTitle
        },
        subtitle: {
          text: "huidige week is: " + currentWeek
        },
        legend: {
          layout: "vertical",
          align: "right",
          verticalAlign: "middle"
        },
        xAxis: {
          labels: {
            useHTML: true,
            formatter: function() {
              return "<span class='hastip'>" + this.value + "</span>";
            }
          },
          plotLines: [
            {
              value: currentWeek,
              width: 1,
              color: "green",
              label: {
                text: "Nu"
              }
            }
          ]
        },
        yAxis: {
          min: 0,
          title: {
            text: ""
          }
        },
        tooltip: {
          enabled: true
        },
        plotOptions: {
          series: {
            pointStart: 1
          }
        },
        series: [
          {
            data: series1,
            name: "2016"
          },
          {
            data: series2,
            name: "2017"
          },
          {
            data: series3,
            name: "2018"
          },
          {
            data: series4,
            name: "2019"
          }
        ]
      };
    }
  }
};
</script>

<style scoped>
p {
  margin: 15px 0 0 15px;
}
</style>


