import Vue from "vue";
import Router from "vue-router";
import Hello from "@/components/Hello";
import Sales from "@/components/Sales";
import Signin from "../components/auth/signin.vue";
import Slacker from "@/components/Slacker";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Signin",
      component: Signin,
    },
    {
      path: "/Signout",
      name: "Signout",
      component: Signin,
    },
    {
      path: "/Hello",
      name: "Hello",
      component: Hello,
    },
    {
      path: "/Sales",
      name: "Sales",
      component: Sales,
    },
    {
      path: "/Slacker",
      name: "Slacker",
      component: Slacker,
    },
  ],
});



